import {
    BaseElement,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
} from '@umetrics/sartorius-ui-base';
import { placeholderStyle } from './placeholderStyle';
import { PlaceholderProperties } from './placeholderProperties';
import { version } from './metadata';

@defineElement({ tagName: 'placeholder', styles: placeholderStyle, version })
export class Placeholder extends BaseElement implements PlaceholderProperties {
    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    container = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'on-gray' })
    onGray = false;

    // Private members

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            container: this.container,
            onGray: this.onGray,
        });

        return html`
            <div class="${rootClasses}">
                <p class="${baseClassName}-text-wrapper">
                    <slot class="${baseClassName}-text"></slot>
                </p>
            </div>
        `;
    }
}
