import { css, CSSResultArray, cssSelector, cssVariableAsVar, GenerateStyles, style } from '@umetrics/sartorius-ui-base';

export const placeholderStyle: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    return [
        style,
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            ${root} {
                display: flex;
                width: 100%;
                height: fit-content;
                align-items: flex-start;
                justify-content: center;
                background: transparent;
            }
            ${child('text-wrapper')} {
                margin: 0;
                padding: 36px 3px;
                max-width: 70%;
                line-height: 21px;
                text-align: center;
            }
            ${child('text')} {
                font-size: 14px;
                font-style: italic;
                color: ${cssVariableAsVar('color_secondary_7')};
            }
            ${is('onGray')} ${child('text')} {
                color: ${cssVariableAsVar('color_secondary_8')};
            }
            ${is('container')} {
                height: 100%;
                align-items: center;
            }
        `,
    ] as CSSResultArray;
};
